import React, { useState, useEffect } from 'react';
import './App.css';
import Typography from '@material-ui/core/Typography';
import { useParams, useHistory, Link  } from 'react-router-dom'
import Divider from '@material-ui/core/Divider';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';


import axios from "axios";




function Org() {
  const {id} = useParams();
  const [papers, setPapers] = useState([]);
  const apiCall= "https://osaka.o18s.com:9000/projectpapers/".concat("?organization_name=", id)
  const history = useHistory();

  useEffect(() => {
    refreshList();
  }, [])

  const refreshList = () => {
      axios
        .get(apiCall)
        .then(res => {
            const paper_list = res.data.results;
            setPapers(paper_list);
          })
        .catch(err => console.log(err));
  };
  return (
    <div className="App">

		<Typography variant="h2" gutterBottom>
		  {id}
		</Typography>

      <List>
        { papers.map(paper => 
            <ListItem button>
              <ListItemText primary={paper.title} />
              <Typography variant="h4" gutterBottom>{paper.journal_title} - {paper.journal_year}</Typography>
            </ListItem>
        )}
        <Divider />
      </List>

    </div>
  );
}

export default Org;
